import { NextRequest, NextResponse } from 'next/server'

export function middleware(request: NextRequest) {
  // Protect /private route
  if (request.nextUrl.pathname.startsWith('/private')) {
    const password = request.cookies.get('private_view_auth')?.value

    // If not authenticated, redirect to password gate
    if (!password) {
      return NextResponse.redirect(new URL('/private/gate', request.url))
    }
  }

  return NextResponse.next()
}

export const config = {
  matcher: ['/private/:path*'],
}
